import React from 'react'
import Spinner from '../layout/Spinner'
import Manga from './Manga'

const Mangas = ({ mangas }) => {
	if (mangas === undefined || mangas.length === 0) {
		return <Spinner />
	} else {
		return (
			<>
				{mangas.map((data) => (
					<Manga key={data.id} manga={data} />
				))}
			</>
		)
	}
}

export default Mangas
