import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { mangaData } from '../../data/manga'

const Search = () => {
	let history = useHistory()
	const [searchItem, setSearchItem] = useState('')
	const [mangas, setMangas] = useState(mangaData)

	const searchManga = () => {
		const filtered = mangas.filter((item) => {
			return item.title.toLowerCase().includes(searchItem.toLowerCase())
		})

		setMangas(filtered)

		history.push({
			pathname: '/manga-reader/',
			//search: '?query=' + searchItem,
			state: { mangas: filtered },
		})

		setSearchItem('')
	}

	useEffect(() => {
		console.log(mangas)
	}, [mangas])

	return (
		<>
			<form className='form-inline ml-auto my-2 my-lg-0'>
				<input
					className='form-control mr-sm-2'
					type='search'
					placeholder='Search'
					aria-label='Search'
					value={searchItem}
					onChange={(e) => setSearchItem(e.target.value)}
				/>
				<button
					className='btn btn-outline-success my-2 my-sm-0'
					type='submit'
					onClick={() => searchManga()}
				>
					Search
				</button>
			</form>
		</>
	)
}

export default Search
