import React, { useState, useEffect } from 'react'
import { useLocation } from 'react-router-dom'
import { mangaData } from '../../data/manga'
import Mangas from '../manga/Mangas'

const Home = () => {
	const location = useLocation()
	const [mangas, setMangas] = useState(mangaData)

	useEffect(() => {
		if (location.state !== undefined) {
			setMangas(location.state.mangas)
		}
	}, [location.state])

	return (
		<React.Fragment>
			<div className='row bg-light'>
				<Mangas mangas={mangas} />
			</div>
		</React.Fragment>
	)
}

export default Home
